.navbar {
    background-color: #111;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 20;
    .nav-container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 80px;
        max-width: 1500px;
        .nav-icon {
            display: none;
        }
        .nav-logo {
            color: rgb(37, 150, 190);
            align-items: center;
            margin-left: 20px;
            cursor: pointer;
            text-decoration: none;
            font-size: 2rem;
            flex-grow: 1;
            .fa-code {
                margin-left: 1rem;
            }
        }
        .nav-menu {
            display: flex;
            list-style: none;
            text-align: center;
            margin-right: 2rem;
            .nav-item {
                line-height: 40px;
                margin-right: 1rem;
                &:after {
                    content: "";
                    display: block;
                    height: 3px;
                    width: 0;
                    background: transparent;
                    transition: width 0.7s ease, background-color 0.5s ease;
                }
                &:hover:after {
                    width: 100%;
                    background: rgb(37, 150, 190);
                }
                .active {
                    border: 1px solid black;
                    background-color: rgb(37, 150, 190);
                    border-radius: 8%;
                    color: crimson;
                }
                .nav-links {
                    color: rgb(37, 150, 190);
                    text-decoration: none;
                    padding: 0.5rem 1rem;
                    height: 100%;
                    border-bottom: 3px solid transparent;
                    &.active {
                        color: black;
                    }
                }
            }
        }
        @media screen and (max-width: 960px) {
            .nav-menu {
                display: flex;
                flex-direction: column;
                width: 100%;
                border-top: 2px solid rgb(37, 150, 190);
                position: absolute;
                top: 80px;
                left: -110%;
                opacity: 1;
                transition: all 0.5s ease;
                opacity: 1;
                &.active {
                    background: white;
                    left: 0px;
                    opacity: 1;
                    transition: all 0.5s ease;
                    z-index: 1;
                }
                .nav-item {
                    .active {
                        color: black;
                        border: none;
                    }
                    .nav-links {
                        padding: 1.5rem;
                        width: 100%;
                        display: table;
                    }
                }
            }
            .nav-icon {
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                transform: translate(-100%, 60%);
                font-size: 1.8rem;
                cursor: pointer;
                color: rgb(37, 150, 190);
            }
        }
    }
}
